@import "/src/Responsive.scss";

.library {
    margin-top: 100px;
    position: relative;
    display: flex;
    justify-content: flex-end;
    @include tablet {
        flex-direction: column;
    }
    &__part {
        width: 464px;
        position: absolute;
        left: 0;
        @include tablet {
            position: initial;
            width: 100%;
        }
    }
    &__img {
        width: 84%;
        padding-top: 50px;
        @include desktop-up {padding-top: 220px;}
        @include tablet {padding-top: 32px;}
        @include phone-landscape {
            width: 100%;
            padding-top: 16px;
        }
    }
}