@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.footer {
    display: flex;
    flex-direction: column;
    margin-top: 770px;
    position: relative;
    @include desktop-uppp {
        margin-top: 638px;
    }
    @include desktop-upp {
        margin-top: 538px;
    }
    @include desktop-up {
        margin-top: 438px;
    }
    @include phone-portrait {
        margin-top: 150px;
    }
    p {
        z-index: 2;
    }
    &__content {
        @include flexbox;
        flex-direction: row;
        padding: 32px;
        @include phone-landscape {padding: 24px;}
    }
    &__link {
        z-index: 2;
    }
    .footer__icon.footer__icon-white {
        opacity: .35;
        &:hover {
            opacity: .48;
        }
    }
    &__icon {
        height: 30px;
        width: auto;
        margin-left: 40px;
        z-index: 2;
        opacity: .8;
        @include phone-portrait {
            height: 16px;
            margin-left: 20px;
        }
        &:hover {
            opacity: 1;
        }
    }
    &__bg {
        position: absolute;
        bottom: 0;
        width: 100%;
    }
}
