@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.composition {
    margin-bottom: 128px;
    &__subtitle {
        font-size: $fs-48;
        font-weight: 500;
        opacity: .67;
        text-align: center;
        margin-top: -64px;
        margin-bottom: 64px;
        @include tablet {margin-top: 0;}
        @include phone-landscape {
            font-size: $fs-32;
            margin-top: -32px;
        }
        @include phone-portrait {
            font-size: $fs-24;
            margin-top: -20px;
        }
    }
    &__part {
        grid-column: span 6;
        @include tablet {grid-column: span 12;}
    }
    &__img {
        grid-column: 8 / 13;
        width: 100%;
        @include desktop {
            margin-top: 280px;
        }
        @include tablet {
            grid-column: span 12;
            margin-top: 32px;
        }
    }
}