@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.member-entity {
    @include flexbox;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 0 80px;
    margin-bottom: 120px;
    @include tablet {
        grid-gap: 0 55px;
        margin-bottom: 70px;
    }
    @include phone-portrait {
        grid-gap: 0 32px;
        margin-bottom: 48px;
    }
    @include phone-micro {
        grid-gap: 0 60px;
    }
    @include phone-pico {
        grid-gap: 0 50px;
    }
    &__item {
        grid-column: span 1;
        width: 14%;
        height: 265px;
        opacity: .8;
        @include desktop {width: 18%;}
        @include tablet {width: 25%;}
        @include phone-landscape {width: 32%;}
        @include phone-portrait {
            width: 22%;
            height: 180px;
        }
        @include phone-micro {
            width: 32%;
        }
        @include phone-pico {
            width: 37%;
        }
        @include flexbox;
        flex-direction: column;
        justify-content: flex-start;
        &:hover {
            opacity: 1;
            p {
                display: block;
                font-size: $fs-24;
                @include phone-landscape {
                    font-size: $fs-32;
                }
                @include phone-portrait {
                    font-size: $fs-24;
                }
            }
        }
        p {
            display: none;
        }
        img {
            border-radius: 100%;
        }
    }
}
