// Colors
$beige: #f58d65;
$orange: #f0592d;
$red: #ca3028;
$gray: #86969a;
$blue: #39545f;
$green: #15414c;
$dark: #0d2c34;

// Fonts
// Warning : 1rem = 10px on this project (Html font-size = 16px, 1 rem = 16px base)
// 10px / 16px for a ratio 1:1 px/rem (without calculation) = 0,625
// Font-size of html element = 62,5% and not 100%

$fs-11: 1.1rem; /* 11px */
$fs-12: 1.2rem; /* 12px */
$fs-14: 1.4rem; /* 14px */
$fs-16: 1.6rem; /* 16px */
$fs-18: 1.8rem; /* 18px */
$fs-20: 2rem; /* 20px */
$fs-22: 2.2rem; /* 22px */
$fs-24: 2.4rem; /* 24px */
$fs-28: 2.8rem; /* 28px */
$fs-32: 3.2rem; /* 32px */
$fs-36: 3.6rem; /* 36px */
$fs-40: 4rem; /* 40px */
$fs-48: 4.8rem; /* 48px */
$fs-56: 5.6rem; /* 56px */
$fs-60: 6rem; /* 60px */
$fs-64: 6.4rem; /* 60px */
$fs-72: 7.2rem; /* 72px */
$fs-71: 7.1rem; /* 71px */
$fs-90: 9rem; /* 90px */
$fs-100: 10rem; /* 100px */

// Border
$border-orange: 2px solid #f0592d;
