@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.workingEntity {
    @include flexbox;
    &:first-child {
        margin-bottom: 72px;
        @include tablet {margin-bottom: 64px;}
        @include tablet {margin-bottom: 32px;}
        @include phone-portrait {margin-bottom: 16px;}
    }
    &:nth-child(2) {
        margin-bottom: 50px;
        @include phone-portrait {margin-bottom: 16px;}
    }
    &__desc {
        width: 120px;
        @include phone-portrait {width: 85px;}
    }
    &__number {
        font-size: $fs-48;
        font-weight: 300;
        @include phone-portrait {font-size: $fs-40;}
        span {font-size: $fs-32;}
    }
    &__text {
        font-size: $fs-24;
        font-weight: 500;
        @include phone-portrait {font-size: $fs-16;}
    }
    &__line {
        background: #f0592b;
        width: 70px;
        height: 1.5px;
        margin: 16px 0;
        @include phone-portrait {
            margin: 8px 0;
            width: 35px;
        }
    }
    &__dot {
        width: 28px;
        height: 28px;
        border-radius: 5rem;
        @include phone-portrait {
            width: 14px;
            height: 14px;
        }
    }
}