@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.header {
    padding: 32px 64px;
    position: fixed;
    top: 0;
    z-index: 4;
    &__icon {
        left: 0;
    }
    &__menu {
        right: 0;
    }
    @include flexbox;
    justify-content: space-between;
    @include desktop {padding: 32px;}
    @include phone-landscape {padding: 24px;}
    &__logo {
        height: 80px;
        width: auto;
        &:hover {
            filter: drop-shadow(0 0 0.75rem $gray);
        }
        @include desktop {height: 64px;}
        @include tablet {height: 48px;}
        @include phone-landscape {height: 32px;}
    }
    &__title {
        position: sticky;
        width: 40px;
        left: 70px;
        top: 150px;
        z-index: 3;
        @include desktop {
            width: 32px;
            left: 36px;
            top: 132px;
        }
        @include tablet {
            width: 24px;
            left: 36px;
            top: 110px;
        }
        @include phone-landscape {
            width: 16px;
            left: 26px;
            top: 80px;
        }
        @include phone-portrait {
            width: 10px;
            left: 29px;
            top: 70px;
        }
    }
}
