// Desktop first responsive

// `xxl` returns only a ruleset and no media query
// `xxl` applies to x-large devices (large desktops, less than 1400px)
// @media (max-width: 1399.98px) { ... }
// ... { ... }

// `xxxxl` applies to very very large devices (desktops-up, less than 2300px)
@mixin desktop-uppp {
    @media (max-width: 2299.98px) {@content;}
}

// `xxxl` applies to very very large devices (desktops-up, less than 1838px)
@mixin desktop-upp {
    @media (max-width: 1837.98px) {@content;}
}

// `xxl` applies to very large devices (desktops-up, less than 1560px)
@mixin desktop-up {
    @media (max-width: 1559.98px) {@content;}
}
// `xl` applies to large devices (desktops, less than 1200px)
@mixin desktop {
    @media (max-width: 1199.98px) {@content;}
}
// `lg` applies to medium devices (tablets, less than 992px)
@mixin tablet {
    @media (max-width: 991.98px) {@content;}
}
// `md` applies to small devices (landscape phones, less than 768px)
@mixin phone-landscape {
    @media (max-width: 767.98px) {@content;}
}
// `sm` applies to x-small devices (portrait phones, less than 576px)
@mixin phone-portrait {
    @media (max-width: 575.98px) {@content;}
}
// `smm` applies to xx-small devices (portrait phones, less than 480px)
@mixin phone-micro {
    @media (max-width: 479.98px) {@content;}
}
// `smmm` applies to xxx-small devices (portrait phones, less than 430px)
@mixin phone-pico {
    @media (max-width: 429.98px) {@content;}
}
// `smmmm` applies to xxxx-small devices (portrait phones, less than 375px)
@mixin phone-nano {
    @media (max-width: 374.98px) {@content;}
}

