@import "/src/Responsive.scss";

.icon {
    position: relative;
    z-index: 3;
    &__grid {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 128px 0;
        @include tablet {
            grid-gap: 64px 0;
        }
        @include phone-portrait {
            grid-gap: 32px 0;
        }
    }
}
