@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.jumbotron {
    @include flexbox;
    justify-content: center;
    background-image: url("../../../public/img/background/jumbotron-bg.svg");
    background-repeat: no-repeat;
    background-size: 101%;
    height: 930px;
    @include desktop-up {height: 820px;}
    @include desktop {height: 770px;}
    @include tablet {height: 580px;}
    @include phone-landscape {height: 420px;}
    @include phone-portrait {
        height: 50vh;
    }
    &__logo {
        height: 210px;
        width: auto;
        margin-right: 72px;
        @include desktop {
            height: 160px;
            margin-right: 48px;
        }
        @include tablet {
            height: 160px;
            margin-right: 48px;
        }
        @include phone-portrait {
            height: 70px;
            margin-right: 32px;
        }
    }
    &__title {
        font-size: $fs-100;
        @include desktop {font-size: $fs-90;}
        @include tablet {font-size: $fs-72;}
        @include phone-landscape {font-size: $fs-56;}
        @include phone-portrait {font-size: $fs-40;}
        span {
            font-size: $fs-100;
            font-weight: 700;
            @include desktop {font-size: $fs-90;}
            @include tablet {font-size: $fs-72;}
            @include phone-landscape {font-size: $fs-56;}
            @include phone-portrait {font-size: $fs-40;}
        }
        span:first-child {
            color: #f05a2d;
        }
    }
    &__text {
        font-size: $fs-48;
        @include tablet {font-size: $fs-36;}
        @include phone-landscape {font-size: $fs-28;}
        @include phone-portrait {font-size: $fs-24;}
        span {
            font-size: $fs-48;
            font-weight: 700;
            @include tablet {font-size: $fs-36;}
            @include phone-landscape {font-size: $fs-28;}
            @include phone-portrait {font-size: $fs-24;}
        }
    }
    &__subtitle {
        font-size: $fs-32;
        font-weight: 700;
        @include tablet { font-size: $fs-28; }
        @include phone-landscape {font-size: $fs-24;}
        @include phone-portrait { font-size: $fs-20; }
    }
}
