@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.map {
    &__graph {
        margin-top: 20px;
        @include flexbox;
        @include tablet {
            margin-top: -100px;
        }
        @include phone-landscape {
            justify-content: space-between;
            align-items: flex-start;
            margin-top: 32px;
        }
    }
    &__circle {
        position: relative;
        margin-left: 20px;
        width: 23%;
        @include phone-landscape {
            margin-left: 0;
            width: 80px;
        }
        p {
            @include absolute;
            font-size: $fs-28;
            text-align: center;
            @include desktop-up {font-size: $fs-24;}
            @include desktop {font-size: $fs-14;}
            @include tablet {font-size: $fs-12;}
        }
        span {
            font-size: $fs-22;
            @include desktop {font-size: $fs-16;}
            @include tablet {font-size: $fs-12;}
        }
    }
    &__draw {
        width: 61%;
        margin-top: 75px;
        @include phone-landscape {
            width: 256px;
            margin-top: initial;
        }
    }
    &__legend {
        margin-top: -75px;
        @include phone-landscape {margin-top: -25px;}
    }
    &__line {
        width: 100%;
        height: 1px;
        background: #ffffff45;
        margin-bottom: 66px;
        @include phone-landscape {margin-bottom: 16px;}
        &:last-child {margin-bottom: 0;}
    }
    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: -167px;
        @include phone-landscape {margin-top: -117px;}
    }
    &__stat {
        width: 14.2857142857%;
        @include flexbox;
        flex-direction: column;
        &:first-child .map__square div {
            height: 59px;
            background: $red;
            @include phone-landscape {height: 14px;}
        }
        &:nth-child(2) .map__square div {
            height: 52px;
            background: $orange;
            @include phone-landscape {height: 10px;}
        }
        &:nth-child(3) .map__square div {
            height: 45px;
            background: $beige;
            @include phone-landscape {height: 8px;}
        }
        &:nth-child(4) .map__square div {
            height: 5px;
            background: #fac3a8;
            @include phone-landscape {height: 3px;}
        }
        &:nth-child(5) .map__square div {
            height: 2px;
            background: #fddfce;
            @include phone-landscape {height: 1px;}
        }
        &:nth-child(6) .map__square div {
            height: 2px;
            background: #fddfce;
            @include phone-landscape {height: 1px;}
        }
    }
    &__square {
        height: 100px;
        margin-bottom: 24px;
        @include flexbox;
        align-items: flex-end;
        @include phone-landscape {margin-bottom: 8px;}
        div {
            width: 52px;
            @include phone-landscape {width: 16px;}
        }
    }
    &__number {
        font-size: $fs-48;
        font-weight: 300;
        @include tablet {font-size: $fs-40}
        @include phone-portrait {font-size: $fs-24}
        span {
            font-size: $fs-32;
            @include tablet {font-size: $fs-24}
            @include phone-portrait {font-size: $fs-20}
        }
    }
    &__border {
        width: 70px;
        height: 1px;
        background: $orange;
        margin: 16px 0;
        @include phone-landscape {
            width: 24px;
            margin: 4px 0;
        }
    }
    &__text {
        font-size: $fs-24;
        font-weight: 500;
        text-align: center;
        @include tablet {font-size: $fs-16;}
        @include phone-portrait {font-size: $fs-11;}
    }
}