@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.list {
    &__item {
        @include flexbox;
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-24;
        line-height: 36px;
        color: $gray;
        padding-left: 75px;
        position: relative;
        margin-bottom: 32px;
        @include desktop {padding-left: 64px;}
        @include phone-landscape {
            line-height: initial;
            padding-left: 48px;
        }
        @include phone-portrait {
            font-size: $fs-20;
            margin-bottom: 24px;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__circle {
        border: 3.5px solid $orange;
        border-radius: 5rem;
        width: 40px;
        height: 40px;
        @include flexbox;
        justify-content: center;
        position: absolute;
        left: 0;
        @include phone-landscape {
            border: 2px solid $orange;
            width: 24px;
            height: 24px;
        }
    }
    &__dot {
        width: 22px;
        height: 22px;
        border-radius: 5rem;
        background: #9d524c;
        @include phone-landscape {
            width: 12px;
            height: 12px;
        }
    }
}