@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.circle {
    margin-bottom: -90px;
    @include desktop-up {margin-bottom: 0;}
    @include desktop {margin-bottom: -90px;}
    @include phone-landscape {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    &__part {
        @include flexbox;
        position: relative;
        @include phone-landscape {margin-bottom: 64px;}
        &:nth-child(3) {
            justify-content: flex-end;
            margin-top: -110px;
            @include desktop-up {margin-top: -20px;}
            @include tablet {margin-top: 50px;}
            @include phone-landscape {margin-top: initial;}
        }
        &:nth-child(4) {
            justify-content: center;
            width: 570px;
            margin-left: 310px;
            margin-top: -50px;
            @include desktop-up {margin-top: 40px;}
            @include desktop {margin-bottom: 100px;}
            @include tablet {
                margin-top: -60px;
                margin-left: 60px;
            }
            @include phone-landscape {
                margin-top: initial;
                margin-left: initial;
                width: initial;
            }
        }
    }
    &__number {
        font-size: $fs-48;
        font-weight: 300;
        @include phone-portrait {font-size: $fs-36;}
        span {
            font-size: $fs-32;
            @include phone-portrait {font-size: $fs-24;}
        }
    }
    &__line {
        display: block;
        height: 1px;
        background: white;
        width: 180px;
        @include phone-landscape {width: 156px;}
    }
    &__text {
        font-size: $fs-24;
        font-weight: 500;
        margin-top: 2px;
        @include phone-portrait {font-size: $fs-20;}
    }
    &__stat {
        @include phone-landscape {
            position: absolute;
        }
    }
    &__graph {
        position: relative;
        p {
            text-transform: uppercase;
            font-size: $fs-32;
            font-weight: 500;
            color: $orange;
            text-align: center;
            @include absolute;
            @include phone-landscape {font-size: $fs-24;}
            @include phone-portrait {font-size: $fs-16;}
        }
        img {
            position: relative;
            z-index: 1;
        }
    }
}
.circle__part:nth-child(2) {
    @include desktop {margin-bottom: 176px;}
    @include tablet {
        margin-left: -34px;
        margin-bottom: 72px;
    }
    @include phone-landscape {margin-bottom: 32px;}
}
.circle__part:nth-child(3) {
    @include desktop {margin-bottom: 136px;}
    @include tablet {margin-right: -30px;}
    @include phone-landscape {margin-bottom: 32px;}
}
.circle__part:nth-child(2) .circle__stat:nth-child(2),
.circle__part:nth-child(2) .circle__stat:nth-child(3),
.circle__part:nth-child(3) .circle__stat:nth-child(2) {
    position: absolute;
    text-align: right;
}
.circle__part:nth-child(4) .circle__stat:nth-child(3),
.circle__part:nth-child(4) .circle__stat:nth-child(4) {
    position: absolute;
}
.circle__part:nth-child(4) .circle__stat:nth-child(2),
.circle__part:nth-child(3) .circle__stat:nth-child(4) {
    text-align: right;
}
.circle__part:nth-child(3) .circle__stat:nth-child(4) {
    margin-top: 10px;
    margin-left: -1px;
}
.circle__part:nth-child(2) .circle__stat:first-child .circle__line {
    width: 160px;
    @include phone-landscape {width: 80px;}
    @include phone-portrait {width: 90px;}
}
.circle__part:nth-child(3) .circle__stat:first-child .circle__line {width: 120px;}
.circle__part:nth-child(4) .circle__stat:nth-child(2) .circle__line,
.circle__part:nth-child(4) .circle__stat:nth-child(3) .circle__line {
width: 134px;
}
.circle__part:nth-child(2) {
    .circle__stat:first-child {
        @include phone-landscape {
            top: 50px;
            left: -80px;
        }
    }
    .circle__stat:nth-child(2) {
        top: -16px;
        left: 454px;
        @include phone-landscape {
            left: 120px;
            top: -26px;
        }
        @include phone-portrait {
            left: 50px;
        }
    }
    .circle__stat:nth-child(3) {
        top: 110px;
        left: 524px;
        @include tablet {left: 480px;}
        @include phone-landscape {
            top: 44px;
            left: 140px;
        }
        @include phone-portrait {
            top: 24px;
            left: 100px;
        }
        .circle__line {
            @include phone-portrait {left: 74px;}
        }
    }
}
.circle__part:nth-child(3) {
    .circle__stat:first-child {
        @include phone-landscape {
            top: 50px;
            left: -80px;
        }
        @include phone-portrait {left: -70px;}
        .circle__line {
            @include phone-landscape {width: 80px;}
        }
    }
    .circle__stat:nth-child(2) {
        top: -16px;
        right: 70px;
        @include phone-landscape {
            right: initial;
            top: -22px;
            left: 110px;
        }
        @include phone-portrait {left: 50px;}
    }
    .circle__stat:nth-child(4) {
        @include phone-landscape {
            position: absolute;
            left: 160px;
        }
        @include phone-portrait {
            left: 110px;
            top: 23px;
        }
        .circle__line {
            @include phone-portrait {width: 96px;}
        }
    }
}
.circle__part:nth-child(4) {
    .circle__stat:nth-child(2) {
        top: 49px;
        left: 164px;
        @include phone-portrait {left: 94px;}
        .circle__line {
            @include phone-landscape {width: 100px;}
        }
    }
    .circle__stat:nth-child(3) {
        top: -16px;
        left: -4px;
        @include tablet {left: -3px;}
        @include phone-landscape {
            top: -17px;
            left: -124px;
        }
        @include phone-portrait {
            top: -14px;
            left: -104px;
        }
        .circle__line {
            @include phone-landscape {width: 146px;}
        }
    }
    .circle__stat:nth-child(4) {
        top: 30px;
        left: -75px;
        @include tablet {left: -74px;}
        @include phone-landscape {left: -45px;}
        @include phone-portrait {top: 14px;}
        .circle__line {
            @include phone-landscape {width: 49px;}
        }
    }
}
.circle__part:nth-child(2) .circle__graph img {width: 364px;}
.circle__part:nth-child(3) .circle__graph img {width: 302px;}
.circle__part:nth-child(4) .circle__graph img {width: 235px;}
.circle__part:nth-child(2) .circle__graph img,
.circle__part:nth-child(3) .circle__graph img,
.circle__part:nth-child(4) .circle__graph img {
    @include phone-landscape {width: 164px;}
    @include phone-portrait {width: 116px;}
}
