@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.article {
    margin-bottom: 128px;
    position: relative;
    @include desktop {margin-bottom: 96px;}
    @include tablet {margin-bottom: 80px;}
    &__title {
        border-bottom: 2px solid $orange;
        padding-bottom: 24px;
        font-size: $fs-56;
        font-weight: 500;
        @include desktop {font-size: $fs-48;}
        @include tablet {font-size: $fs-40;}
        @include phone-portrait {font-size: $fs-24}
    }
    &__collapsed {
        display: none;
    }
    &__expanded {
        display: block;
    }
    ul {
        padding-left: 45px;
        margin: 60px 0;
        @include phone-landscape {
            padding-left: 0;
            margin: 32px 0;
        }
    }
}
