@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.testimonial {
    @include flexbox;
    flex-direction: column;
    position: relative;
    &__quote {
        height: 154px;
        margin-bottom: 42px;
        @include tablet {
            height: 104px;
            margin-bottom: 32px;
        }
        @include phone-landscape {
            height: 42px;
            margin-bottom: 16px;
        }
    }
    &__text {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-32;
        margin-bottom: 80px;
        text-align: center;
        @include tablet {
            font-size: $fs-24;
            margin-bottom: 32px;
        }
        @include phone-landscape {
            font-size: $fs-24;
            padding: 0 32px;
        }
        @include phone-portrait {
            margin-bottom: 32px;
            font-size: $fs-20;
        }
    }
    &__job {
        font-size: $fs-24;
        font-weight: 500;
        color: #f05a2c;
        margin-bottom: 8px;
        text-align: center;
        @include phone-landscape {font-size: $fs-20;}
    }
    &__name {
        font-size: $fs-40;
        font-weight: 300;
        @include phone-landscape {font-size: $fs-24;}
    }
}
