@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.cardEntity {
    grid-column: span 4;
    @include flexbox;
    flex-direction: column;
    &__top {
        width: 100%;
    }
    &__bg {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: white;
        width: 100%;
        padding: 30px;
        border-radius: 24px;
        height: 370px;
        margin-top: -128px;
        padding-top: 0;
        @include desktop-up {margin-top: -96px;}
        @include desktop {
            margin-top: -100px;
            height: 440px;
        }
        @include tablet {
            width: 100%;
            margin-top: -88px;
        }
        @include phone-landscape {
            height: 304px;
            margin-top: -104px;
        }
    }
    &__quote {
        width: 38px;
        margin-bottom: 34px;
    }
    &__text {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-16;
        color: $blue;
        text-align: center;
    }
    &__job {
        font-size: $fs-20;
        font-weight: 300;
        color: $orange;
    }
    &__name {
        font-size: $fs-14;
        font-weight: 700;
        color: $orange;
    }

}