@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.community {
    padding-bottom: 430px;
    margin-top: 60px;
    @include desktop-up {padding-bottom: 220px;}
    @include desktop {padding-bottom: 200px;}
    @include tablet {padding-bottom: 160px;}
    @include phone-landscape {padding-bottom: 70px;}
    @include phone-portrait {padding-bottom: 160px;}
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 70px;
        @include desktop {flex-direction: column;}
    }
    &__imgwrap {
        display: inline-block;
        height: 100%;
        width: 50%;
        @include desktop {
            width: 100%;
            overflow: hidden;
            height: 400px;
            margin-bottom: 32px;
        }
        @include tablet {
            height: 300px;
        }
        @include phone-landscape {
            height: 250px;
        }
    }
    &__img {
        @include desktop {
            transform: translateY(-15%);
        }
        @include tablet {
            width: 100%;
        }
    }
    &__text {
        width: 45%;
        @include desktop {width: 100%;}
    }
    &__bottom {
        @include flexbox;
        justify-content: space-between;
        position: relative;
        @include tablet {flex-direction: column;}
    }
    &__stat {
        width: 24%;
        @include desktop {width: 30%;}
        @include tablet {
            width: 100%;
            margin-bottom: 48px;
        }
        @include phone-portrait {margin-bottom: 24px;}
        &:last-child {
            @include tablet {margin-bottom: 0;}
        }
    }
    &__number {
        font-size: $fs-64;
        font-weight: 300;
        @include phone-portrait {font-size: $fs-40;}
    }
    &__line {
        display: block;
        height: 2px;
        width: 100%;
        background: $orange;
        margin: 26px 0;
        @include phone-portrait {margin: 16px 0;}
    }
    &__subtext {
        font-size: $fs-24;
        font-weight: 500;
        @include tablet {font-size: $fs-20;}
    }
}
