@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.btn {
    background: none;
    border: $border-orange;
    color: white;
    height: 60px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 64px;
    border-radius: 16px;
    font-size: $fs-24;
    position:relative;
    cursor: pointer;
    &__large {
        height: 120px;
        font-size: $fs-56;
        @include tablet {
            height: 80px;
            font-size: $fs-40;
        }
        @include phone-landscape {
            height: 60px !important;
            padding: 0 42px !important;
            border-radius: 16px !important;
            font-size: $fs-36 !important;
        }
        @include phone-portrait {
            height: 50px !important;
            padding: 0 32px !important;
            border-radius: 16px !important;
            font-size: $fs-24 !important;
        }
    }
    @include phone-landscape {
        height: 40px;
        padding: 0 32px;
        border-radius: 12px;
    }
    @include phone-portrait {
        font-size: $fs-20;
    }
    z-index: 1;
    &:before {
        content:'';
        -webkit-transition:0.5s all ease;
        transition:0.5s all ease;
        position:absolute;
        top:0;
        left:50%;
        right:50%;
        bottom:0;
        opacity:0;
        border-radius: 12px;
        background-color: $orange;
        z-index:-2;
    }
    &:hover {
        &:before {
            -webkit-transition:0.5s all ease;
            transition:0.5s all ease;
            left:0;
            right:0;
            opacity:1;
        }
    }
    &:focus {
        &:before {
            transition:0.5s all ease;
            left:0;
            right:0;
            opacity:1;
        }
    }

}
