@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.members {
    margin-top: 110px;
    &__part {
        margin-bottom: 110px;
        @include phone-portrait {
            margin-bottom: 32px;
        }
    }
    &__group {
        display: grid;
        grid-template-columns: repeat(10, 1fr);
        grid-gap: 64px;
        align-items: center;
        @include tablet {grid-gap: 32px;}
        @include phone-portrait {
            grid-gap: 16px;
            align-items: initial;
        }
    }
    &__title {
        font-size: $fs-48;
        font-weight: 500;
        margin-bottom: 80px;
    }
    img {
        max-width: auto;
        grid-column: span 2;
        max-height: 64px;
        @include tablet {max-height: 48px;}
        @include phone-landscape {grid-column: span 5;}
        @include phone-portrait {
            height: 24px!important;
            margin-right: 32px!important;
            margin-bottom: 32px;
        }
        &:nth-child(n+6) {
            margin-bottom: 0;
            @include phone-portrait {
                margin-bottom: 32px;
            }
        }
    }
}
