@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.growing {
    margin-top: 290px;
    position: relative;
    @include desktop {margin-top: 128px;}
    @include phone-landscape {margin-top: 80px;}
    h2 {
        @include phone-portrait {
            margin-bottom: 32px;
        }
    }
    &__part {
        grid-column: span 4;
        @include flexbox;
        flex-direction: column;
        @include phone-portrait {
            grid-column: span 12;
            margin-bottom: 32px;
        }
    }
    &__number {
        font-size: $fs-64;
        font-weight: 300;
        margin-bottom: 64px;
        @include tablet {margin-bottom: 32px;}
        @include phone-landscape {
            font-size: $fs-32;
            margin-bottom: 0;
        }
        @include phone-portrait {margin-bottom: 8px;}
    }
    &__subtitle {
        font-size: $fs-40;
        font-weight: 300;
        margin-bottom: 55px;
        @include tablet {font-size: $fs-24}
        @include phone-landscape {margin-bottom: 16px;}
    }
    &__graph {
        width: 232px;
        
        @include tablet {width: 90%;}
        @include phone-portrait {width: 50%;}
    }
    &__legend {
        margin-top: 48px;
        width: 100%;
        padding-left: 80px;
        @include desktop-up {padding-left: 24px;}
        @include desktop {padding-left: 0;}
        @include phone-landscape {margin-top: 16px;}
    }
    &__text {
        display: inline-flex;
        align-items: center;
        &:first-child {
            margin-bottom: 20px;
            @include tablet {margin-right: 32px;}
            @include phone-landscape {margin-bottom: 8px;}
        }
        &:nth-child(2) {
            margin-left: 30px;
            @include tablet {
                margin-left: 0;
                margin-bottom: 20px;
            }
            @include phone-landscape {margin-bottom: 8px;}
        }
        span {
            display: block;
            width: 18px;
            height: 18px;
            border-radius: 5rem;
            margin-right: 18px;
            @include phone-landscape {
                width: 12px;
                height: 12px;
                margin-right: 12px;
            }
        }
        p {
            font-size: $fs-24;
            font-weight: 500;
            @include desktop {width: calc(100% - 36px);}
            @include tablet {font-size: $fs-16;}
        }
    }
}