@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.working {
    position: relative;
    z-index: 2;
    margin-top: 140px;
    &__body {
        display: flex;
        justify-content: space-between;
        @include tablet {
            flex-direction: column;
        }
    }
    &__bg {
        position: absolute;
        width: 300px;
        top: -120px;
    }
    &__part {
        @include desktop-up {
            width: 44%;
        }
        @include tablet {
            width: 100%;
        }
        &:first-child {
            @include tablet {
                margin-bottom: 64px;
            }
        }
        &:first-child .working__desc {margin-right: 24px;}
        &:nth-child(2) .working__desc {margin-left: 24px;}
        &:first-child .working__stat {margin-right: 48px;}
        &:nth-child(2) .working__stat {margin-left: 48px;}
        > div {
            @include tablet {
                justify-content: center;
            }
        }
    }
    &__group {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: 1fr 1fr;
        align-items: center;

        @include tablet {
            grid-gap: 32px;
        }

        @include phone-portrait {
            grid-gap: 16px;
            align-items: initial;
        }
    }
    &__subtitle {
        font-size: $fs-40;
        font-weight: 500;
        border-bottom: 2px solid #f0592b;
        padding-bottom: 36px;
        margin-bottom: 75px;
        text-align: center;
        @include phone-portrait {
            padding-bottom: 24px;
            margin-bottom: 32px;
            font-size: $fs-28;
        }
    }
    &__graph {
        max-width: 290px;
        @include desktop-up { max-width: 250px; }
        img {
            width: 90%;
            @include tablet {
                width: 100%;
            }
        }
    }
    &__meter {
        margin-top: 30px;
        > div:first-child {
            display: flex;
            justify-content: space-around;
            padding: 0 5px;
            > div {
                height: 34px;
                width: 2px;
                background: #5e7278;
                @include phone-portrait {
                    height: 16px;
                }
            }
        }
        > div:nth-child(2) {
            display: flex;
            justify-content: space-between;
            margin-top: 12px;
            > p {
                font-size: $fs-24;
                width: 36px;
                text-align: center;
                color: #5e7278;
            }
        }
    }
}
.working__part:nth-child(2) .workingEntity__desc {
    order: 2;
    margin-left: 24px;
}
