@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.badge {
    @include flexbox;
    margin-bottom: 16px;
    &:nth-child(3) {margin-bottom: 64px;}
    &__number {
        background: $orange;
        width: 64px;
        height: 64px;
        border-radius: 5rem;
        @include flexbox;
        justify-content: center;
        font-size: $fs-32;
        font-weight: 300;
        margin-right: 32px;
        @include phone-landscape {
            margin-right: 16px;
            width: 40px;
            height: 40px;
            font-size: $fs-24;
        }
    }
    &__text {
        font-size: $fs-40;
        font-weight: 300;
        text-transform: uppercase;
        width: calc(100% - 96px);
        @include phone-landscape {
            font-size: $fs-32;
            width: calc(100% - 80px);
        }
        @include phone-portrait {font-size: $fs-24;}
    }
}