@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.text__primary {
    font-family: 'Zilla Slab', sans-serif;
    font-size: $fs-24;
    line-height: 32px;
    color: $gray;
    > span {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-24;
        line-height: 32px;
        color: lighten($gray, 20%);
    }
    @include phone-landscape {line-height: 28px;}
    @include phone-portrait {
        font-size: $fs-20;
        line-height: 20px;
    }
}

.text__calltoaction {
    font-size: $fs-18;
    line-height: 32px;
    color: white;

    &:hover {
        -webkit-transition: 0.5s all ease;
        transition: 0.5s all ease;
        color: $orange;
        text-decoration: underline;
    }

    @include phone-landscape {
        line-height: 28px;
    }

    @include phone-portrait {
        font-size: $fs-16;
        line-height: 20px;
    }
}
