@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.clamp {
    margin-bottom: 128px;
    position: relative;
    @include phone-portrait {
        margin-bottom: 64px;
    }
    &__title {
        border-bottom: 2px solid $orange;
        padding-bottom: 24px;
        font-size: $fs-56;
        font-weight: 500;
        @include phone-landscape {font-size: $fs-40;}
        @include phone-portrait {font-size: $fs-32;}
    }
    &__content {
        padding: 0 45px;
        margin: 60px 0;
        @include phone-portrait {
            margin: 24px 0;
            padding: 0;
        }
    }
    &__collapsed {display: none;}
    &__expanded {display: block;}
}
