@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.gearing {
    display: flex;
    justify-content: center;
    height: 808px;
    padding-top: 10px;
    position: relative;
    margin-top: 180px;
    @include desktop {
        flex-direction: column;
        height: initial;
        align-items: center;
        padding: 0 32px;
        margin-top: 64px;
    }
    &__gen {
        @include flexbox;
        flex-direction: column;
        position: relative;
    }
    &__part {
        @include flexbox;
    }
    &__text {
        position: absolute;
        font-size: $fs-24;
        font-weight: 500;
        color: $gray;
        @include phone-landscape {font-size: $fs-24;}
        @include phone-portrait {font-size: $fs-20;}
    }
    &__line {
        background: #f05a2c;
    }
    &__draw {
        position: relative;
    }
    &__logo {
        @include absolute;
    }
    &__arrow {
        width: 302px;
        position: absolute;
        @include desktop-up {width: 198px;}
        @include desktop {
            width: 240px;
            position: initial;
        }
        @include phone-portrait {width: 140px;}
    }
    &__gen1 {
        @include desktop {
            flex-direction: row;
        }
        @include phone-portrait {align-items: flex-end;}
        .gearing__part:first-child {
            flex-direction: column;
            @include desktop-up {
                position: absolute;
                left: -94px;
            }
            @include desktop {
                position: initial;
                order: 2;
            }
            @include phone-landscape {margin-top: 30px;}
            @include phone-portrait {
                margin-top: 0;
                margin-bottom: 15px;
            }
            .gearing__text {
                top: -15px;
                left: 292px;
                @include desktop-up {left: 230px;}
                @include desktop {
                    position: initial;
                    left: 132px;
                }
            }
            .gearing__draw {
                .gearing__icon {
                    width: 100%;
                    animation-name: spin-reverse;
                    animation-duration: 15.5s;
                    animation-iteration-count: infinite;
                }
            }
            .gearing__line:nth-child(2) {
                width: 84px;
                height: 2px;
                margin-left: 82px;
                @include desktop {display: none;}
            }
            .gearing__line:nth-child(3) {
                width: 2px;
                height: 77px;
                @include desktop {height: 32px;}
            }
        }
        .gearing__part:nth-child(2) {
            position: absolute;
            top: 540px;
            left: -10px;
            @include desktop-up {
                left: -76px;
                top: 554px;
            }
            @include desktop {
                position: initial;
                flex-direction: column;
                margin-right: 48px;
            }
            @include phone-landscape {margin-right: 32px;}
            @include phone-portrait {margin-right: 24px;}
            .gearing__text {
                position: initial;
                text-align: center;
                margin-right: 16px;
                @include desktop {margin-right: 0;}
            }
            .gearing__line {
                width: 84px;
                height: 2px;
                margin-top: 10px;
                @include desktop {
                    width: 2px;
                    height: 84px;
                }
                @include phone-portrait {
                    width: 2px;
                    height: 32px;
                }
            }
            .gearing__draw {
                .gearing__icon {
                    animation-name: spin-reverse;
                    animation-duration: 15.5s;
                    animation-iteration-count: infinite;
                }
            }
        }
    }
    &__gen2 {
        top: 180px;
        left: -22px;
        @include desktop-up {
            top: 190px;
            left: 150px;
        }
        @include desktop {
            top: initial;
            left: initial;
            margin-top: -40px;
            flex-direction: row;
        }
        @include phone-landscape {margin-top: -30px;}
        @include phone-landscape {margin-top: -20px;}
        .gearing__text {
            width: 127px;
            top: -50px;
            left: 75px;
            @include desktop-up {
                width: 100px;
                top: -46px;
                left: 90px;
                transform: rotate(6deg);
            }
            @include desktop {
                top: initial;
                left: initial;
                transform: rotate(-70deg);
                margin-left: 0px;
                width: 140px;
            }
            @include phone-landscape {
                margin-left: 12px;
                width: 116px;
            }
            @include phone-portrait {
                margin-left: 2px;
                width: 76px;
            }
        }
        .gearing__icon {
            animation-name: spin;
            animation-duration: 15.5s;
            animation-iteration-count: infinite;
        }
        .gearing__arrow:nth-child(2) {
            top: -26px;
            left: 48px;
            @include desktop-up {
                top: -18px;
                left: 58px;
            }
            @include desktop {
                transform: rotate(-80deg);
            }
        }
        .gearing__arrow:last-child {
            top: 412px;
            right: -14px;
            transform: rotate(164deg);
            @include desktop-up {
                top: 330px;
                right: 2px;
            }
            @include desktop {
                top: 224px;
                right: -34px;
                transform: rotate(100deg);
            }
        }
        .gearing__draw {
            @include phone-portrait {
                margin: 0 -60px;
            }
        }
    }
    &__gen3 {
        left: -140px;
        @include desktop-up {
            left: 20px;
            top: 12px;
        }
        @include desktop {
            left: initial;
            flex-direction: row;
            margin-top: -4px;
            margin-left: 70px;
        }
        .gearing__text {
            top: -15px;
            left: -142px;
            @include desktop {
                position: initial;
                order: 3;
                left: -110px;
            }
        }
        .gearing__line:nth-child(2) {
            width: 2px;
            height: 59px;
            @include desktop {display: none;}
        }
        .gearing__line:nth-child(3) {
            position: absolute;
            width: 78px;
            height: 2px;
            left: 23px;
            @include desktop-up {width: 52px;}
            @include desktop {
                position: initial;
                order: 2;
                width: 32px;
                margin-right: 8px;
            }
        }
        .gearing__draw {
            order: 1;
        }
        .gearing__icon {
            animation-name: spin-reverse-fast;
            animation-duration: 15.5s;
            animation-iteration-count: infinite;
        }
    }
    &__gen4 {
        left: -160px;
        top: 42px;
        @include desktop-up {
            left: 4px;
        }
        @include desktop {
            left: initial;
            top: initial;
            flex-direction: row;
            margin-right: 24px;
        }
        @include phone-landscape {
            left: 30px;
            top: -10px;
        }
        .gearing__text {
            position: initial;
            margin-bottom: 8px;
            @include desktop {
                margin-bottom: 0;
                position: absolute;
                left: -158px;
                top: 212px;
            }
            @include phone-portrait {
                left: -130px;
                top: 122px;
            }
        }
        .gearing__line:nth-child(2) {
            width: 2px;
            height: 42px;
            @include desktop {
                height: 200px;
                left: -20px;
                top: 128px;
                position: absolute;
            }
            @include phone-portrait {
                height: 116px;
                top: 76px;
            }
        }
        .gearing__line:nth-child(3), .gearing__line:nth-child(4), .gearing__line:nth-child(5) {
            position: absolute;
        }
        .gearing__line:nth-child(3) {
            width: 222px;
            height: 2px;
            top: 15px;
            left: 212px;
            @include desktop-up {
                width: 135px;
                left: 182px;
            }
            @include desktop {
                width: 18px;
                top: 128px;
                left: -20px;
            }
            @include phone-portrait {top: 75px;}
        }
        .gearing__line:nth-child(4) {
            width: 2px;
            height: 441px;
            top: 15px;
            left: 432px;
            @include desktop-up {
                left: 315px;
                height: 363px;
            }
            @include desktop {
                width: 21px;
                height: 2px;
                top: 326px;
                left: -20px;
            }
            @include phone-portrait {top: 191px;}
        }
        .gearing__line:nth-child(5) {
            width: 78px;
            height: 2px;
            top: 454px;
            left: 355px;
            @include desktop-up {
                width: 46px;
                top: 376px;
                left: 270px;
            }
            @include desktop {display: none;}
        }
        .gearing__draw:nth-child(6) {
            .gearing__icon {
                animation-name: spin;
                animation-duration: 15.5s;
                animation-iteration-count: infinite;
            }
        }
        .gearing__draw:nth-child(7) {
            position: absolute;
            top: 354px;
            left: 152px;
            @include desktop-up {
                top: 300px;
                left: 120px;
            }
            @include desktop {
                top: 252px;
                left: 0;
            }
            @include phone-portrait {top: 142px;}
            .gearing__icon {
                animation-name: spin-reverse-fast;
                animation-duration: 15.5s;
                animation-iteration-count: infinite;
            }
        }
    }
}
.gearing__gen1 .gearing__part:first-child .gearing__logo, .gearing__gen4 .gearing__draw:nth-child(6) .gearing__logo {width: 50%;}
.gearing__gen1 .gearing__part:nth-child(2) .gearing__logo, .gearing__gen2 .gearing__logo, .gearing__gen3 .gearing__logo, .gearing__gen4 .gearing__draw:nth-child(7) .gearing__logo {width: 44%;}

.gearing__gen1 .gearing__part:first-child .gearing__draw {
    width: 396px;
    @include desktop-up {width: 278px;}
    @include phone-landscape {width: 250px;}
    @include phone-portrait {width: 180px;}
}
.gearing__gen1 .gearing__part:nth-child(2) .gearing__draw {
    width: 295px;
    @include desktop-up {width: 230px;}
    @include phone-landscape {width: 210px;}
    @include phone-portrait {width: 120px;}
}
.gearing__gen2 .gearing__draw {
    width: 490px;
    @include desktop-up {width: 380px;}
    @include desktop {margin: 0 -120px;}
    @include phone-landscape {width: 330px;}
    @include phone-portrait {
        width: 200px;
        margin: 0 -68px;
    }
}
.gearing__gen3 .gearing__draw {
    width: 203px;
    @include desktop-up {width: 150px;}
    @include phone-portrait {width: 100px;}
}
.gearing__gen4 .gearing__draw:nth-child(6) {
    width: 295px;
    @include desktop-up {width: 230px;}
    @include desktop {top: 16px;}
    @include phone-portrait {width: 120px;}
}
.gearing__gen4 .gearing__draw:nth-child(7) {
    width: 203px;
    @include desktop-up {width: 150px;}
    @include phone-portrait {width: 100px;}
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes spin-reverse {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}
@keyframes spin-reverse-fast {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-720deg);
    }
}
