@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.achievement {
    position: relative;
    margin-top: 180px;
    @include phone-portrait {
        padding: initial;
        margin-top: 64px;
    }
    &__category {
        display: flex;
        justify-content: flex-end;
        &:first-child .achievement__content, &:nth-child(3) .achievement__content {
            align-items: flex-end;
            text-align: right;
        }
        &:first-child {
            grid-column: 1 / 4;
            margin-bottom: 132px;
            @include tablet {
                grid-column: 1/5;
            }
            @include phone-portrait {
                margin-bottom: 0;
            }
        }
        &:nth-child(2) {
            grid-column: 10 / 13;
            justify-content: start;
            @include tablet {
                grid-column: 9 / 13;
            }
            @include phone-portrait {
                margin-top: 20px;
            }
            .achievement__content {
                @include phone-landscape {margin-top: 50px;}
                @include phone-portrait {margin-top: 10px;}
            }
        }
        &:nth-child(3) {
            grid-column: 1 / 4;
            @include tablet {
                grid-column: 1 / 5;
            }
            @include phone-landscape {
                order: 4;
                margin-top: 0;
            }
            @include phone-portrait {
                margin-top: -50px;
            }
        }
        &:nth-child(4) {
            grid-column: 10 / 13;
            justify-content: start;
            @include tablet {
                grid-column: 9 / 13;
            }
            @include phone-landscape {
                order: 5;
                margin-top: 60px;
            }
            @include phone-portrait {
                margin-top: 0;
            }
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        @include phone-portrait {
            width: 100%;
        }
    }
    &__subtitle {
        font-size: $fs-32;
        font-weight: 500;
        opacity: .8;
        &:hover {opacity: 1;}
        @include phone-portrait {font-size: $fs-24;}
    }
    &__line {
        background: #f0592b;
        width: 232px;
        height: 1.5px;
        margin: 16px 0;
        @include desktop {
            width: 100%;
        }
    }
    &__number {
        font-size: $fs-48;
        font-weight: 300;
        color: #f05a2d;
        @include phone-portrait {font-size: $fs-36;}
    }
    &__text {
        font-size: $fs-24;
        font-weight: 500;
        @include phone-portrait {font-size: $fs-20;}
    }
    &__graph {
        position: relative;
        text-align: center;
        background-image: url("../../../public/img/Fichier\ 14.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        @include flexbox;
        flex-direction: column;
        justify-content: center;
        grid-column: 5 / 9;
        grid-row: 1 / 3;
        @include tablet {
            width: 332px;
            background-size: auto 320px;
        }
        @include phone-landscape {
            grid-column: span 12;
            order: 3;
            grid-row: initial;
            width: 100%;
            height: 500px;
            background-size: 80%;
            margin-top: -50px;
        }
        @include phone-portrait {
            background-size: 80%;
        }
    }
    &__uppercase {
        font-size: $fs-28;
        text-transform: uppercase;
        font-weight: 500;
        margin-bottom: 30px;
        @include desktop-up {font-size: $fs-20;}
        @include phone-landscape {font-size: $fs-32;}
        @include phone-portrait {font-size: $fs-20;}
    }
    &__price {
        @include flexbox;
        font-size: $fs-48;
        color: #f05a2d;
        font-weight: 300;
        @include desktop-up {font-size: $fs-20;}
        @include tablet {font-size: $fs-32;}
        @include phone-landscape {font-size: $fs-56;}
        @include phone-portrait {font-size: $fs-36;}
        span {
            font-size: $fs-20;
            text-transform: uppercase;
            margin-top: 2px;
        }
    }
    &__icon {
        position: absolute;
        &:nth-child(4) {
            width: 140px;
            top: -26px;
            left: -58px;
            @include desktop-up {
                width: 100px;
                top: 61px;
                left: -41px;
            }
            @include desktop {
                width: 80px;
                top: 111px;
                left: -33px;
            }
            @include tablet {
                width: 84px;
                left: 2px;
                top: 105px;
            }
            @include phone-landscape {
                width: 110px;
                top: -4px;
                left: -9px;
            }
            @include phone-portrait {
                width: 80px;
                top: 75px;
                left: -7px;
            }
        }
        &:nth-child(5) {
            width: 158px;
            top: 52px;
            right: -67px;
            @include desktop-up {
                width: 130px;
                top: 117px;
                right: -55px;
            }
            @include desktop {
                width: 100px;
                top: 157px;
                right: -42px;
            }
            @include tablet {
                width: 110px;
                top: 154px;
                right: -9px;
            }
            @include phone-landscape {
                top: 55px;
                right: -25px;
                width: 150px;
            }
            @include phone-portrait {
                width: 100px;
                top: 116px;
                right: -16px;
            }
        }
        &:nth-child(6) {
            width: 92px;
            bottom: 26px;
            left: -45px;
            @include desktop-up {
                width: 70px;
                bottom: 102px;
                left: -35px;
            }
            @include desktop {
                width: 54px;
                bottom: 145px;
                left: -25px;
            }
            @include tablet {
                width: 56px;
                bottom: 144px;
                left: 14px;
            }
            @include phone-landscape {
                width: 80px;
                bottom: 32px;
                left: 0;
            }
            @include phone-portrait {
                width: 54px;
                bottom: 102px;
                left: 2px;
            }
        }
        &:nth-child(7) {
            width: 105px;
            bottom: -42px;
            right: -18px;
            @include desktop-up {
                width: 80px;
                bottom: 47px;
                right: -13px;
            }
            @include desktop {
                width: 60px;
                bottom: 84px;
                right: -9px;
            }
            @include tablet {
                width: 72px;
                bottom: 84px;
                right: 25px;
            }
            @include phone-landscape {
                bottom: -22px;
                right: 22px;
                width: 90px;
            }
            @include phone-portrait {
                width: 60px;
                bottom: 66px;
                right: 16px;
            }
        }
    }
}
