@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.form {
    height: 100vh;
    @include flexbox;
    justify-content: center;
    &__content {
        @include flexbox;
        flex-direction: column;
        width: 24%;
    }
    &__label {
        font-size: $fs-18;
        width: 100%;
        margin-bottom: 32px;
    }
    &__input {
        font-size: $fs-20;
        background: white;
        color: black;
        height: 48px;
        margin-top: 8px;
        border-radius: 8px;
        padding: 12px;
        width: 100%;
    }
    &__submit {
        background: none;
        border: $border-orange;
        color: white;
        height: 48px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        border-radius: 8px;
        font-size: $fs-20;
        &:hover {
            opacity: .8;
        }
    }
}