@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.scala3release {
    padding-bottom: 90px;
    margin-top: 100px;
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include desktop {flex-direction: column;}
    }
    &__video {
        width: 45%;
        height: 100%;
        > iframe {
            // youtube embed
            position: relative;
            height: 405px;
            @include desktop {height: 365px;}
            @include phone-landscape {height: 255px;}
        }
        @include desktop {width: 100%;}
    }
    &__text {
        width: 53%;
        @include desktop {
            width: 100%;
            margin-bottom: 32px;
        }
    }
}
