@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.tooling {
    position: relative;
    @include tablet {margin-top: 0;}
    @include phone-landscape {margin-top: 32px;}
    &__part {
        grid-column: span 3;
        padding-top: 150px;
        @include desktop-up {padding-top: 0;}
        @include tablet {grid-column: span 6;}
        @include phone-landscape {grid-column: span 12;}
        &:first-child {
            @include tablet {order: 2;}
        }
        &:nth-child(3) {
            @include tablet {order: 3;}
        }
    }
    &__content {
        margin-bottom: 58px;
        display: flex;
        flex-direction: column;
        @include tablet {align-items: center!important;}
        @include phone-landscape {margin-bottom: 40px!important;}
        &:last-child {margin-bottom: 0;}
    }
    &__logo {
        margin-bottom: 16px;
        height: 80px;
        width: auto;
        @include phone-landscape {height: 64px;}
        @include phone-portrait {height: 48px;}
    }
    &__dot {
        display: block;
        width: 28px;
        height: 28px;
        background: $orange;
        border-radius: 5rem;
        z-index: 2;
    }
    &__text {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-16;
        color: $gray;
        text-align: justify;
        @include tablet {text-align: center!important;}
        @include phone-landscape {font-size: $fs-24;}
        @include phone-portrait {font-size: $fs-20;}
    }
    &__draw {
        @include flexbox;
        justify-content: space-between;
        grid-column: span 6;
        background-image: url("../../../public/img/background/body-bg.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 70%;
        height: 762px;
        @include desktop-up {
            justify-content: center;
        }
        @include tablet {
            order: 1;
            grid-column: span 12;
            background-size: 68%;
            margin-bottom: 32px;
        }
        @include phone-landscape {
            height: 470px;
            background-size: 80%;
        }
        @include phone-portrait {height: 300px;}
        > div {
            position: relative;
            height: 460px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-top: 40px;
            &:first-child {
                margin-left: -10px;
                @include desktop-up {display: none;}
            }
            &:last-child {
                margin-right: -14px;
                height: 480px;
                margin-top: 70px;
                @include desktop-up {display: none;}
            }
        }
        > img {
            width: 450px;
            z-index: 3;
            @include desktop {width: 420px;}
            @include phone-landscape {width: 70%;}
        }
    }
    &__line {
        position: absolute;
    }
}
.tooling__part:first-child .tooling__content {
    align-items: flex-end;
    .tooling__text {text-align-last: right;}
}
.tooling__part:first-child .tooling__content:first-child {
    @include tablet {margin-bottom: 110px;}
}
.tooling__part:last-child .tooling__content {
    align-items: flex-start;
    &:first-child {
        margin-bottom: 70px;
    }
}
.tooling__part:first-child .tooling__content:nth-child(2) .tooling__logo {
    height: 70px;
    @include desktop-up {width: 100%;}
    @include tablet {height: 40px;}
    @include phone-landscape {height: 56px;}
}
.tooling__draw > div:first-child .tooling__line:first-child {
    width: 310px;
    top: -47px;
    left: 5px;
}
.tooling__draw > div:first-child .tooling__line:nth-child(2) {
    width: 142px;
    top: 23px;
    left: 18px;
}
.tooling__draw > div:first-child .tooling__line:nth-child(3) {
    width: 222px;
    top: 83px;
    left: 6px;
}
.tooling__draw > div:last-child .tooling__line:first-child {
    width: 239px;
    top: 14px;
    left: -212px;
}
.tooling__draw > div:last-child .tooling__line:nth-child(2) {
    width: 100px;
    top: 240px;
    left: -100px;
}
.tooling__draw > div:last-child .tooling__line:nth-child(3) {
    width: 115px;
    top: 423px;
    left: -90px;
}
