@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.post {
    position: relative;
    z-index: 2;
    &__title {
        text-align: left;
        margin-bottom: 50px;
    }
    &__intro {
        width: 47%;
        margin-bottom: 60px;
        @include tablet {width: 100%;}
    }
    &__entity {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 90px;
        margin-bottom: 80px;
        @include desktop-up {grid-gap: 32px;}
        @include desktop {grid-gap: 24px;}
        @include tablet {grid-gap: 32px;}
        @include phone-landscape {
            grid-gap: 24px;
            margin-bottom: 48px;
        }
        &:last-child {margin-bottom: 0;
}
    }
    &__img {
        grid-column: span 6;
        width: 100%;
        border: 10px solid $green;
        @include tablet {
            grid-column: span 12;
            order: 2;
        }
    }
    &__content {
        grid-column: span 6;
        @include tablet {grid-column: span 12;}
    }
    &__head {
        display: flex;
        margin-bottom: 40px;
        @include tablet {margin-bottom: 24px;}
        @include phone-landscape {margin-bottom: 16px;}
    }
    &__number {
        font-size: $fs-72;
        font-weight: 300;
        @include phone-landscape {font-size: $fs-56;}
    }
    &__subtitle {
        font-size: $fs-40;
        font-weight: 500;
        color: $orange;
        border-top: 2px solid $orange;
        width: 100%;
        padding-top: 20px;
        margin-top: 7px;
        margin-left: 40px;
        @include phone-landscape {
            font-size: $fs-32;
            margin-left: 16px;
            padding-top: 9px;
        }
    }
}