@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.card {
    max-width: 1300px;
    margin-top: -200px;
    margin-bottom: 100px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    @include desktop-up {max-width: 930px;}
    @include desktop {padding: 0 112px;}
    @include tablet {
        overflow-x: scroll;
        margin-top: 0;
        margin-bottom: 80px;
        padding-left: 112px;
        padding-right: 112px;
    }
    &__content {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 96px;
        @include desktop-up {grid-gap: 32px;}
        @include desktop {grid-gap: 24px;}
        @include tablet {width: 768px;}
    }
}
