@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include flexbox;
    flex-direction: column;
    justify-content: space-between;
    opacity: 0;
    transition: var(--transition);
    background: white;
    border-radius: 24px;
    padding: 60px;
    @include phone-landscape {padding: 24px 22px;}
    @include phone-portrait {padding: 24px 10px;}
    @include phone-micro {padding: 24px 8px;}
    @include phone-pico {padding: 24px 6px;}
    @include phone-nano {padding: 24px 4px;}
    &__quote {
        height: 55px;
        margin-bottom: 40px;
        @include tablet {
            height: 45px;
            margin-bottom: 30px;
        }
        @include phone-landscape {
            height: 32px;
            margin-bottom: 24px;
        }
    }
    &__text {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-24;
        color: #39545f;
        @include tablet {font-size: $fs-22;}
        @include phone-portrait {font-size: $fs-20;}
    }
    &__job {
        color: $orange;
        font-size: $fs-24;
        margin-top: 50px;
    }
    &__name {
        font-size: $fs-40;
        font-weight: 300;
        color: #39545f;
        @include phone-landscape {font-size: $fs-32;}
    }
}

.activeSlide {
    opacity: 1;
    transform: translateX(0);
}

.lastSlide {
    transform: translateX(-100%);
}

.nextSlide {
    transform: translateX(100%);
}
