@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.navigation {position: relative;}
.navigation-checkbox {display: none;}
.navigation-button, .navigation-background {
    height: 64px;
    width: 64px;
    border-radius: 50%;
    @include desktop {
        height: 48px;
        width: 48px;
    }
    @include phone-portrait {
        height: 32px;
        width: 32px;
    }
}
.navigation-button {
    text-align: center;
    background-color: #fff;
    z-index: 300;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.2);
    &:hover {
            filter: drop-shadow(0 0 0.75rem $gray);
        }
    cursor: pointer;
    position: absolute;
    display: flex;
    justify-content: center;
}

.navigation-background {
    background: #0d2c34;
    z-index: 100;
    transition: all 800ms cubic-bezier(0.86, 0, 0.07, 1);
}
.nav-motion {
    z-index: 999;
    text-align: center;
}
.nav-icon {
    display: flex;
    align-items: center;
}
.nav-icon > span {
    color: #000000;
    @include phone-portrait {font-size: $fs-20;}
}
.nav-icon > div > svg:first-child {display: none!important;}
.container-nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    visibility: hidden;
    z-index: 200;
}
.navigation-item {margin: 1rem;}
.navigation-link:link,
.navigation-link:visited {
    display: inline-block;
    padding: 1rem 2rem;
    color: #f4f4f4;
    font-size: $fs-24;
    text-decoration: none;
}
.navigation-link:hover {color: #c1d0d0;}
.navigation-checkbox:checked~.navigation-background {transform: scale(100);}
.navigation-checkbox:checked~.container-nav {
    width: 100vw;
    visibility: visible;
    display: flex;
    align-items: center;
}
