@import "/src/Variables.scss";
@import "/src/Responsive.scss";

h2 {
    font-size: $fs-72;
    font-weight: 500;
    text-align: center;
    margin-bottom: 80px;
    @include desktop {font-size: $fs-64;}
    @include tablet {
        font-size: $fs-56;
        margin-bottom: 48px!important;
    }
    @include phone-landscape {font-size: $fs-48;}
    @include phone-portrait {
        font-size: $fs-36;
        margin-bottom: 32px!important;
    }
}