@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.intro {
    display: flex;
    align-items: center;
    flex-direction: column;
    &__title {
        font-size: $fs-64;
        margin-top: 64px;
        @include phone-landscape {
            font-size: 4rem;
            margin-top: 16px;
        }
    }
    &__border {
        height: 2.5px;
        width: 160px;
        background: $orange;
        margin: 60px auto;
        @include phone-landscape {
            margin: 32px auto;
        }
    }
    &__bottom {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-32;
        color: $orange;
        text-align: center;
        padding: 0 140px;
        @include desktop {padding: 0;}
        @include phone-landscape {font-size: $fs-28;}
        @include phone-portrait {font-size: $fs-20;}
    }
}
