@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.arrow {
    @include flexbox;
    flex-direction: column;
    margin-bottom: 64px;
    @include desktop {margin-top: 50px;}
    @include tablet {margin-top: 70px;}
    @include phone-landscape {
        order: 3;
        margin-top: 16px;
        margin-bottom: 16px;
    }
    &__line {
        height: 375px;
        width: 2px;
        background: $orange;
        display: block;
        @include phone-landscape {height: 48px;}
    }
    &__triangle {
        width: 0; 
        height: 0; 
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-top: 23px solid $orange;
    }
}