@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.iconEntity {
    &__part {
        grid-column: span 4;
        @include flexbox;
        flex-direction: column;
        @include tablet {grid-column: span 6;}
    }
    &__img {
        height: 100px;
        width: auto;
        margin-bottom: 60px;
        @include tablet {
            margin-bottom: 32px;
        }
        @include phone-landscape {
            height: 64px;
            margin-bottom: 0;
        }
    }
    &__title {
        font-size: $fs-24;
        font-weight: 500;
        margin-bottom: 24px;
        @include tablet {font-size: $fs-20;}
        @include phone-landscape {margin-top: 8px;}
        span {margin-left: 16px;}
    }
    &__number {
        font-size: $fs-48;
        font-weight: 300;
        @include phone-landscape {font-size: $fs-40;}
        @include phone-portrait {font-size: $fs-24;}
    }
    &__border {
        height: 2px;
        width: 60%;
        background: $orange;
        margin: 24px 0;
        @include phone-landscape {
            margin: 12px 0;
            width: 80%;
        }
    }
    &__text {
        font-size: $fs-24;
        font-weight: 500;
        color: $gray;
        text-align: center;
        width: 70%;
        @include phone-landscape {font-size: $fs-20;}
    }
}