@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.part {
    @include flexbox;
    flex-direction: column;
    margin-top: 210px;
    position: relative;
    @include tablet {margin-top: 80px;}
    &__content {
        display: flex;
        justify-content: space-between;
        @include tablet {flex-direction: column;}
    }
    &__entity {
        width: 42%;
        @include tablet {width: 100%;}
        &:last-child {
            @include tablet {
                order: 2;
                margin-top: 64px;
            }
        }
    }
    &__subtitle {
        margin-bottom: 80px;
        font-size: $fs-48;
        font-weight: 500;
        @include desktop {margin-bottom: 40px;}
        @include phone-landscape {font-size: $fs-32;}
        @include phone-portrait {font-size: $fs-28;}
        
    }
    &__middle {
        margin-top: 140px;
        position: relative;
        @include tablet {
            order: 3;
            margin-top: 32px;
            margin-bottom: 32px;
        }
    }
}