@import "/src/Responsive.scss";

.container {
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 24px;
    @include desktop-up {
        max-width: 100%;
        padding: 0 144px;
    }
    @include desktop {
        max-width: 100%;
        padding: 0 112px;
    }
    @include phone-landscape {padding: 0 96px;}
    @include phone-portrait {padding: 0 64px;}
}