@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.variety {
    background-image: url("../../../public/img/background/body-bg6.svg");
    background-repeat: no-repeat;
    background-position: center 230px;
    background-size: 50%;
    position: relative;
    @include phone-landscape {margin-top: 0;}
    &__text {
        text-align: center;
        margin-bottom: 90px;
    }
    &__content {
        display: flex;
        justify-content: space-between;
        @include phone-landscape {flex-direction: column;}
    }
    &__part {
        width: 390px;
        @include desktop {width: 44%;}
        @include phone-landscape {
            @include flexbox;
            flex-direction: column;
        }
        &:first-child {
            @include phone-landscape {margin-bottom: 80px;}
        }
        img {
            margin-bottom: 56px;
            @include phone-landscape {
                width: 40%;
                margin-bottom: 16px;
            }
        }
        @include phone-landscape {width: 100%;}
        p {text-align: center;}
    }
}
