@import "/src/Variables.scss";
@import "/src/Responsive.scss";

.comparison {
    &__top {
        margin-bottom: 100px;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        @include tablet {flex-direction: column;}
        @include phone-portrait {
            flex-direction: column;
            margin-bottom: 32px;
        }
        > * {
            width: 42%;
            @include tablet {width: 100%;}
        }
        > div:first-child {
            @include tablet {margin-bottom: 32px;}
        }
    }
    &__content {
        display: flex;
        justify-content: space-between;
        @include phone-portrait {
            flex-direction: column;
        }
        & div {
            width: 42%;
            @include phone-portrait {width: 100%;}
        }
        & div:first-child {
            margin-bottom: 32px;
        }
    }
    img {
        width: 100%;
        margin-bottom: 30px;

    }
    &__subtitle {
        text-transform: uppercase;
        font-size: $fs-32;
        font-weight: 500;
        text-align: center;
        z-index: 2;
        position: relative;
    }
}