@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.slider {
    @include flexbox;
    margin-bottom: 160px;
    @include phone-landscape {margin-bottom: 80px;}
    &__view {
        margin: 0 auto;
        width: 960px;
        height: 675px;

        @include tablet {
            height: 745px;
        }

        @include phone-landscape {
            height: 550px;
        }

        @include phone-portrait {
            height: 450px;
        }
        @include phone-micro {
            height: 510px;
        }
        @include phone-pico {
            height: 600px;
        }

        @include phone-nano {
            height: 800px;
        }
        text-align: center;
        position: relative;
        display: flex;
        overflow: hidden;
    }
    &__prev, &__next {
        border-radius: 0.25rem;
        cursor: pointer;
        transition: all 0.3s linear;
        transform: translateY(-50%);
        &:hover {opacity: .8;}
        span {
            font-size: $fs-48;
            @include phone-landscape {font-size: $fs-32;}
        }
    }
    &__next {
        @include phone-landscape {margin-left: 4px;}
    }
}
