@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.result {
    position: relative;
    @include flexbox;
    flex-direction: column;
    &__content {
        display: flex;
        justify-content: space-between;
        @include phone-landscape {
            flex-direction: column;
            margin-bottom: 16px;
        }
    }
    &__part {
        width: 42%;
        @include phone-landscape {
            width: initial;
            margin-bottom: 32px;
            text-align: center;
        }
        &:first-child {
            text-align: right;
            @include phone-landscape {text-align: initial;}
        }
        &:nth-child(3) {
            order: 2;
            @include phone-landscape {text-align: initial;}
        }
    }
    &__title {
        font-size: $fs-56;
        font-weight: 300;
        margin-bottom: 72px;
        @include phone-landscape {
            font-size: $fs-40;
            margin-bottom: 32px;
        }
    }
    &__text {
        font-family: "Zilla Slab", sans-serif;
        font-size: $fs-24;
        line-height: 36px;
        color: #86969a;
        @include phone-landscape {line-height: initial;}
    }
    &__attributed {
        margin-top: 20px;
        @include flexbox;
        flex-direction: row;
        @include phone-landscape {
            flex-direction: column;
        }
        justify-content: right;
    }
    &__quote {
        width: 35px;
        height: 20px;
        margin: 5px;
    }
    &__author {
        font-family: 'Zilla Slab', sans-serif;
        font-size: $fs-20;
        color: rgba(255, 255, 255, 1);
    }
}
