@import "/src/Variables.scss";
@import "/src/Mixins.scss";
@import "/src/Responsive.scss";

.collapse {
    margin-top: 180px;
    background: url("../../../public/img/illustrations/illustration29.svg"), $orange;
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 52%;
    display: flex;
    flex-direction: column;
    position: relative;
    @include desktop-up {
        background-size: 70%;
    }
    @include desktop {
        background-size: 83%;
    }
    @include tablet {
        margin-top: 48px;
        background-size: 108%;
    }
    @include phone-landscape {
        margin-top: 48px;
    }
    &__item {
        @include flexbox;
        transition: 0.3s ease;
        overflow: hidden;
        flex-direction: column;
        &:nth-child(2), &:nth-child(3), &:nth-child(4) {
            .collapse__icon p {
                display: none;
            }
        }
    }
    &__button {
        @include flexbox;
        width: 100%;
    }
    &__icon {
        background: $red;
        color: $orange;
        height: 150px;
        width: 10%;
        font-size: $fs-60;
        cursor: pointer;
        &:hover {
            color: rgba(255, 255, 255, 1);
        }
        @include flexbox;
        justify-content: center;
        @include desktop-up {
            font-size: $fs-56;
        }
        @include tablet {
            font-size: $fs-40;
        }
        @include phone-landscape {
            font-size: $fs-56;
            width: 15%;
            height: 100px;
        }
        @include phone-portrait {
            height: 64px;
            font-size: $fs-40;
        }
        > span {
            font-size: $fs-64;
        }
    }
    &__title {
        @include flexbox;
        padding-left: 32px;
        width: 90%;
        height: 150px;
        font-size: $fs-71;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.6);
        word-break: break-word;
        cursor: pointer;
        @include phone-landscape {
            font-size: $fs-48;
            width: 85%;
            height: 100px;
        }
        @include phone-portrait {
            font-size: $fs-28;
            height: 64px;
        }
        &:hover {
            color: rgba(255, 255, 255, 1);
        }
    }
    &__content {
        background: white;
        width: 100%;
        padding: 100px 144px;
        color: black;
        @include phone-landscape {
            padding: 32px;
        }
    }
    &__entity {
        padding-bottom: 64px;
        border-bottom: $border-orange;
        margin-bottom: 64px;
        &:last-child {
            margin-bottom: 0;
            border-bottom: none;
            padding-bottom: 0;
        }
    }
    &__subtitle {
        font-size: $fs-60;
        color: $orange;
        font-weight: 500;
        margin-bottom: 64px;
        @include phone-landscape {font-size: $fs-40;}
    }
    &__text {
        font-size: $fs-20;
        text-align: justify;
        color: $dark;
        width: 60%;
        line-height: 32px;
        margin-bottom: 10px;
        @include tablet {width: 100%;}
        @include phone-landscape {
            line-height: 24px;
        }
        @include phone-portrait {
            line-height: 24px;
            width: 100%;
        }
    }
    &__btn {
        font-size: $fs-22;
        color: $orange;
        font-weight: 500;
        border: $border-orange;
        border-radius: 5rem;
        height: 48px;
        padding: 0 16px;
        @include flexbox;
        justify-content: center;
        margin-bottom: 10px;
        @include desktop-up {
            font-size: $fs-20;
        }
        @include desktop {
            font-size: $fs-18;
        }
        @include phone-landscape {margin-top: 32px;}
    }
    &__links {
        width: 37%;
        @include flexbox;
        align-items: normal;
        flex-direction: column;
        @include tablet {width: 100%;}
    }
}
.hidden {display: none;}
